
.rml-popup {
  color: white;
}

.rml-popup--back {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 15px;
  display: block;
  opacity: 0;
  left: 2.3rem;
  cursor: pointer;
  transform: rotate(45deg);
  //margin: -22px;
  transition: opacity 150ms ease-in-out;
  &:before, &:after {
    content: ' ';
    position: absolute;
  }

  &:after {
    bottom: 0;
    left: 0;
    margin-top: 0;
    height: 2px;
    width: 50%;
    border-bottom: 2px solid white;
  }
  &:before {
    bottom: 0;
    /* top: 0; */
    height: calc(50% + 1px);
    width: 2px;
    margin-left: -1px;
    border-left: 2px solid white;
  }
  z-index: 100;
}

.rml-popup--close {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 15px;
  right: 15px;
  display: inline-block;
  cursor: pointer;
  transform: rotate(45deg);
  //margin: -22px;
  &:before, &:after {
    content: ' ';
    position: absolute;
    box-sizing: border-box;
  }
  &:after {
    top: 50%;
    left: 0;
    margin-top: -1px;
    height: 2px;
    width: 100%;
    border-bottom: 2px solid white;
  }
  &:before {
    left: 50%;
    top: 0;
    height: 100%;
    width: 2px;
    margin-left: -1px;
    border-left: 2px solid white;
  }
  z-index: 100;
}

.rml-popup--back__show {
  display: block;
  opacity: 1;
}

.rml-popup--back__hide {
  display: none;
}

.rml-md-modal {
  max-width: 630px;
  min-width: 320px;
  height: auto;
  z-index: 2000;
  /** INLINE IT OTHERWISE IT JUMPS THROUGH THE WORLD
  visibility: hidden;
  **/
  display: table-cell;
  backface-visibility: hidden;
  vertical-align: middle;
}

.rml-md-show {
  visibility: visible !important;
}

/* Effect 1: Fade in and scale up */
.rml-md-effect-1 .rml-md-content {
  transform: scale(0.7);
  opacity: 0;
  transition: all $animationDuration;
}

.rml-md-show.rml-md-effect-1 .rml-md-content {

  transform: scale(1);
  opacity: 1;
}

.rml-md-content {
  color: #fff;
  padding: 2rem 2rem 4.5rem 2rem;
  background: rgba(0, 0, 0, 0.8);
  position: relative;
  border-radius: 3px;
  margin: 0 auto;
  box-shadow: 3px 3px 10px 0px rgba(0, 0, 0, 0.3);
  max-width: 515px;
  min-height: $hintSlideDownSize;
  pointer-events: all;
}

.rml-popup__opened .rml-md-overlay {
  opacity: 1;
  visibility: visible;
  display: block;
  width: 100%;
  height: 100%;
}

.rml-md-overlay {
  position: fixed;
  visibility: hidden;
  display: none;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  z-index: 2000;
  opacity: 0;
  background: rgba(0, 0, 0, 0.2);
  transition: all $animationDuration;
}
