$break-small: 500px;
$break-medium: 800px;

.rml-btn-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  visibility: visible;
  overflow: auto;
  z-index: 1000;
}

.rml-popup__closed {
  max-width: 0;
  max-height: 0;
  visibility: hidden;
  overflow: hidden;
}

.rml-btn-wrapper.rml-popup__closed {
  transition: max-width $animationDummyDuration linear $animationDuration, max-height $animationDummyDuration linear $animationDuration, visibility $animationDummyDuration linear $animationDuration;
}

.rml-btn {
  position: relative;
  width: 100%;
  height: 100%;
  visibility: visible;
  display: table;
  z-index: 3000;
  pointer-events: none;
  $slideDownSize: 100px;
  .rml-deeplink---icon {

  }

  .rml-deeplink--hint {
    visibility: hidden;
    &.rml-hint__show {
      visibility: visible;
      //display: block;
      transform: translate3d(0, 0, 0);
      margin-bottom: 0px;
      opacity: 1;
      transition: transform ease-in-out 300ms, margin-bottom ease-in-out 300ms, opacity ease-in-out 200ms 100ms;
      z-index: auto;
    }
    transform: translate3d(0, -$hintSlideDownSize, 0);
    margin-bottom: -$hintSlideDownSize;
    opacity: 0;

    //display: none;
    transition: transform ease-in-out 300ms, margin-bottom ease-in-out 300ms, opacity ease-in-out 150ms;
    z-index: -1;
  }

  .rml-deeplink--hint-link {
    display: block;
    color: $red;
  }

  .rml-deeplink--heading {
    font-size: 18px;
  }

  .rml-deeplink--row, .rml-custom-content {
    max-width: 320px;
    margin: 0 auto;
  }

  .rml-deeplink--row {
    &:focus {
      text-decoration: none;
    }
    &:hover {
      color: #fefefe;
      text-decoration: none;
    }

    color: white;
    align-items: center;
    border-bottom: 1px solid $red;
    margin-top: 50px;
    margin-bottom: 35px;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;

  }

  .rml-deeplink--text {

  }

  .rml-deeplink--icon {
    flex-grow: 1;
    text-align: right;
    svg {
      fill: #fff;
      width: 60px;
      height: 60px;
    }
  }
}

.rml-deeplink--link {
  display: block;
  margin-top: 10px;
  margin-bottom: 15px;
  line-height: 26px;
  height: 26px;
  position: relative;
  input[type="text"] {
    background: white;
    border: none;
    cursor: pointer;
    color: $red;
    padding: 3px;
    margin-right: 6px;
    border-radius: 3px;
    line-height: 26px;
    height: 26px;
    width: 100%;
    font-size: 18px;
    padding-right: 30px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  input[type="text"]:focus {
    outline: none;
  }
  @media screen and (max-width: $break-medium) {
    text-align: center;
  }
}

.rml-deeplink--links {
}

.rml-deeplink--slider {
  width: 200%;
  transition: all 250ms ease-in-out;

  &:after {
    clear: left;
  }
}

.rml-deeplink--slide {
  width: 50%;
  float: left;
}

.rml-generated-content {
  overflow: hidden;
}

.rml-deeplink__hint {
  transform: translate(-50%, 0);
  & > :first-child {
    opacity: 0;
    visibility: hidden;

  }
  .rml-deeplink--row {
    max-width: 100%;
  }
  .rml-deeplink--slide.rml-deeplink--alternate {
    /*padding-left: 195px;
    background-image: url(https://a.storyblok.com/f/cf57e7bb5c/iphone.png);
    background-repeat:no-repeat;
    height: 500px;
    background-size: 150px;
    background-position: 0 50px;*/
  }
}

.rml-deeplink__hint-container {
  display: flex;
  flex-flow: row;

  @media screen and (max-width: $break-medium) {
    flex-flow: column-reverse;
  }
  .rml-deeplink--row {
    border: none;
  }
}

.rml-icons__copy {
  width: 16px;
  height: 16px;
  fill: $red;
  position: absolute;
  top: 5px;
  right: 7px;
}

.rml-deeplink__hint-container {
  .rml-deeplink__hint-img {
    width: 45%;
    margin-top: 50px;
    padding-right: 5%;
    text-align: center;
    flex: 0 0 auto;

    img {
      max-width: 100%;
      max-height: none;
    }
    @media screen and (max-width: $break-medium) {
      margin-top: 0;
      margin: 0 auto;
      width: 100%;
      img {
        max-height: 165px;
        width: auto;
        display: none;
      }
    }
  }
}

.rml-deeplink__hint-link {
  flex: 0 0 auto;
  width: 50%;
  @media screen and (max-width: $break-medium) {
    width: 100%;
  }
}

.rml-deeplink__qr {
  width: 60%;
  max-width: 140px;
  margin: 0 auto;
  padding-bottom: 15px;
  svg {
    border: 5px solid white; // NEEDED OTHERWISE QR CODE READER CAN NOT READ THE CODE
  }
}
