.roomle-buttons {
  visibility: visible !important;
  ul {
    margin: 5px 5px 0 0;
    padding: 0;
    list-style: none;
    max-width: 32px;
    visibility: visible;
  }
  li {
    cursor: pointer;
    svg {
      fill: $red;
      width: 32px;
      height: 32px;

    }
  }

  [data-rml-action="trigger"]:active {
    transform: translate3d(-1px, 1px, 0);
  }
}
