$dark-blue: #363b4a;
$gray: #f0f4f5;
$spinner-lite-side: #d4d4db;
$spinner-dark-side: #545a6a;
.rml-spinner {
  width: 20px;
  height: 20px;
  display: block;
  margin: 0 auto;
  &:after {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    animation: rotate .6s linear infinite;
    border-radius: 100%;
    border-top: 1px solid $spinner-dark-side;
    border-bottom: 1px solid $spinner-lite-side;
    border-left: 1px solid $spinner-dark-side;
    border-right: 1px solid $spinner-lite-side;
    content: '';
    opacity: .5;
  }
  &.rml-spinner--large {
    width: 40px;
    height: 40px;
  }
  &.rml-spinner--medium {
    width: 30px;
    height: 30px;
    border-width: 2px;
  }
  &.rml-spinner--small {
    width: 10px;
    height: 10px;
  }
  // Color variations
  &.rml-spinner--red {
    &:after {
      opacity: 1;
      border-top-color: darken($red, 7%);
      border-bottom-color: rgba($red,.5);
      border-left-color: darken($red, 7%);
      border-right-color: rgba($red,.5);
    }
  }
  // Color variations
  &.rml-spinner--white {
    &:after {
      opacity: 1;
      border-top-color: #fff;
      border-bottom-color: rgba(255,255,255,.5);
      border-left-color: #fff;
      border-right-color: rgba(255,255,255,.5);
    }
  }
}

// Animation
@keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}
@-webkit-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}
@-moz-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}
@-o-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}
