/**
*   GLOBAL SETTINGS
**/
$container-width: 1200px;
$spacer: 20px;

/**
*   Colors
**/
$red: #dd1818;
$grey: #757575;
$grey-darker: #1d1d1d;
$dark-white: #f1f1f1;
$grey-little-bit-darker: #1b1b1b;

/**
*   Font Definitions
**/
$main-font-family: "Lato", "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$main-font-color: $grey-darker;

/**
*   Header
**/
$header-height: 65px;

/**
*   Collapse
**/
$collapse-width: 220px;
$collapse-menu-width: 670px;

/**
*   Spacings
**/
$spacing-big: 65px;
$spacing-medium: 30px;
$spacing-small: 15px;

/**
*  Breakpoints
**/
$screen-sm-min: 544px;
$screen-md-min: 768px;
$screen-lg-min: 992px;
$screen-xl-min: 1200px;

/**
*  Bootstrap Overrides
**/
$enable-flex: true;
$grid-gutter-width: 30px;

/**
*  Font Awesome Overrides
**/
$fa-font-path: "/fonts";

/**
*  RoomleButton specific
**/

$hintSlideDownSize: 150px;

$animationDuration: 300ms;
$animationDummyDuration: 1ms;
